/*
  Agency Fonts Usage
-------------------
 Light - 300
 Regular - 400
 Medium - 500
 Semi bold - 600
 Bold - 700
*/
$fontPath : "../css/fonts";

/* Webfont: BillyBold */
@font-face {
    font-family: 'Billy';
    src: url('#{$fontPath}/BillyBold/Billy-Bold.otf') format('truetype'), /* Safari, Android, iOS */
         url('#{$fontPath}/BillyBold/Billy-Bold.eot'), /* IE 9+ font fix */
         url('#{$fontPath}/BillyBold/Billy-Bold.eot?#iefix') format('embedded-opentype'), /* IE 8 and below font fix */
         url('#{$fontPath}/BillyBold/Billy-Bold.ttf') format('truetype');
    font-style: normal;
    font-weight: $fontBold;
    text-rendering: optimizeLegibility;
}

/* Webfont: Arial Bold */
@font-face {
    font-family: 'ArialRounded';
    src: url('#{$fontPath}/ArialRoundedMTBold.otf') format('truetype'), /* Safari, Android, iOS */
         url('#{$fontPath}/ArialRoundedMTBold.eot'), /* IE 9+ font fix */
         url('#{$fontPath}/ArialRoundedMTBold.eot?#iefix') format('embedded-opentype'), /* IE 8 and below font fix */
         url('#{$fontPath}/ArialRoundedMTBold.ttf') format('truetype');
    font-style: normal;
    font-weight: $fontBold;
    text-rendering: optimizeLegibility;
}