
$videosLanding:(
	main:(
		isViewAllHide: true,
		isRoundedPlayIcon: true,
		playerBarBgnd: $bay-blue,
		playerBarHeight: 0
	),
	homePage:(
		bgnd: url('#{$brandImgPath}/videos_bgnd.jpg') no-repeat top center,
		padding: 0 0 9em 0,
		backgroundSize: cover,
		isPageBgnd: true,
		isBgnd: false, // title Bgnd
		playerPadding: 0,
	),
	videosPage:(

	),
	playIcon: (
        isRoundedCircle: false,
        bgnd:none,
		color:$white,
		fontSize: 65px,
		tabFontSize: 45px,
		mobFontSize: 45px,
		opacity: 1
    ),
	arrows:(
		color:null,
		mobColor: null,
		fontSize: 80px,
		background: null,
		isRoundBgnd: false,
		leftPos:20px,
		rightPos: 20px,
		opacity:1
	),
	nowPlaying: (
		textShadow: 0 14px 48px rgba($black,.23),
		whiteSpace: initial,
		align: center,
		textTransform: none,
		top: 19%,
		lineHeight: 0.9,
		borderRadius: 0,
		padding: 4px 7px,
		margin: 0 auto,
		width: 50%,
		strokeWidth: 4px,
		strokeColor: $bay-blue,
		fontSize: em(59),
		letterSpacing: 0,
		left:0,
		right:0,
		backgroundImage: none,
		backgroundColor: transparent,
		color: $white,
		borderColor: transparent,
		fontFamily: "Billy"
	),
	heading : (
		isDefaultHeadingFont: true,
        bgColor: transparent,
		color : $white,
        fontSize: null,
        fontFamily: null,
		fontWeight: null,
		align: center,
		margin:0,
		padding: 0,
		transform: none
	),
    description : (
		isDefaultDesFont: true,
        bgColor: transparent,
        fontSize: em(20),
        color : $white,
        fontFamily: null,
		fontWeight: null,
		align: center,
		padding: 0,
		margin:em(5,20) 0 0,
	    mobilepadding:0
	),
	player:(
		paddingBottom:calc(56.25% - 8px),
		border:7px solid $violerRed,
		borderRadius: 4px
	),
	playerBar:(
		borderRadius: 20px,
		margin: em(20) 0 0
	),
	playerBarHeading:(
		isDefaultHeadingFont: true,
		color : $white,
        fontSize: em(40),
        fontFamily: null,
		fontWeight: null,
		align: center,
		margin:0,
		padding: 0
	),
	playerBarDescription:(
		isDefaultDesFont: true,
	  	fontSize: em(22),
        color : null,
        fontFamily: null,
		fontWeight: null,
		align: center,
		padding: 0,
	    mobilepadding:0
	),
    thumbnailHeading: (
        bgColor: transparent,
		color : #e0e0e0,
        fontSize: em(40),
        fontFamily: null,
		fontWeight: null,
		align: left,
		padding: em(20,40) em(20,40) 0 0,
		tabPadding: em(20,40) em(20,40) 0,
		mobPadding: em(20,40) em(20,40) 0 0,
		margin: em(20,40) 0 0,
	),
	galleryTile: (
		tilePadding: 0 !important,
		tabTilePadding: 0,
		mobTilePadding: 0
	),
	listTitle : (
		bgColor: $bay-blue,
		color : $white,
        fontSize: em(28),
        fontFamily: 'Billy',
		fontWeight: $fontBold,
		align: left,
		margin:em(15,28) 0 0 ,
		padding: em(15,28) em(20,28),
		borderRadius: 4px
	),
	listOverlay: (
		isOverlayImgAlone: true,
		backgroundColor: rgba($bay-blue, .7)
	),
	showMore: (
		margin:0,
		padding: null
	)
);