$layout:(
	body:(
		font: "Billy",
		fontWeight: $fontBold,
		bgnd: url('#{$brandImgPath}/body_bgnd.jpg'),
		bgndPosition: null,
		bgndSize: 100%
	),
	main: (
		titleMargin: 5rem 3.375rem 3.125rem 3.375rem,
		tabTitleMargin: 1.875rem,
		mobTitleMargin: 1.25rem,
		sectionMargin: 0rem 3.375rem 0 3.375rem,
		tabSectionMargin: 0rem 1.875rem,
		mobSectionMargin: 0rem 1.25rem
	),
	heading:(
		font: "Billy",
		fontWeight: $fontBold,
		fontSize: em(60)	
	),
	description:(
        font:"ArialRounded",
        fontWeight:$fontBold,
        fontSize: em(21)
    ),
	themeBtn:(
        isDefaultHeadingFont: true,
		fontSize: em(21),
		mobFontSize : em(25),
		padding: em(13, 30) em(35, 30) em(15, 30) em(35, 30),
		fontFamily:null,
        fontWeight:null,
        iscurveShape:false,
        bgndColor:$white,
        hoverBgndColor:$dollyGreen !important,
        clipPath:none, //polygon(10% 0 100% 0 100% 100% 0 100% 0 40%),
        color:$razmatRed,
        hoverColor:$violerRed !important,
		border: 2px solid $violerRed !important,
		borderRadius: 10px,
		letterSpacing: 0,
		isnextArrw: false,
		nextContent:"",
		contentFamily:"",
		contentWeight: "",
		contentMargin: ""
	),
	secThemeBtn:(
		bgndColor: $white,
		isArrow: false,
		padding: em(13, 30) em(38, 30) em(15, 30) em(30, 30),
		isPlus: true,
		hoverBgndColor:$dollyGreen,  
		color: $violerRed,
		hoverColor: $violerRed !important,
		border: 2px solid $violerRed,
		ArrowfontSize: 200%,
		ArrowlineHeight: .2
	),
	viewAll: (
		isOverwritePrimaryBtn: true
	),
	dots:(
		fontSize: 14px,
        opacity:1,
        focusColor:$light-green,
        bgndColor:rgba($white, .5),
        activeBgndColor:$white,
        margin:0 6px !important,
		bottomPositon: 23px,
	),
	arrow: (
		color:$pigmentGreen,
        opacity:1,
        isRoundedBgnd:false
  ),
  gototop: (
        bgndColor:#595b5d,
        brdrColor:#6cc04a 1px solid,
        fntColor:$white,
        fntFamily:AgencyFBBlack,
        fntSize:em(17),
        arrowShape:10px solid $white
  ),
  grid:(
		border:7px solid $violerRed,
		borderRadius: 4px
    ),
);