//title
.gallery-headings {
    padding: if(mapping($layout,main,titleMargin), mapping($layout,main,titleMargin), em(55) em(65) em(50) em(65));
    @media #{$medium-down} {
        padding: mapping($layout,main,tabTitleMargin);
    }
    @media #{$small-down} {
        padding: mapping($layout,main,mobTitleMargin);
    }
    h2 {
        @if mapping($gallery,heading,isDefaultHeadingFont) == true {
            font-size: mapping($gallery,heading,fontSize);
        } @else{
            font-family: mapping($gallery,heading,fontFamily);
            font-size: if(mapping($gallery,heading,fontSize),mapping($gallery,heading,fontSize),em(38));
            font-weight: mapping($gallery,heading,fontWeight);
        }
        
        color: if(mapping($gallery,heading,color),mapping($gallery,heading,color),$page-title);
        text-transform: if(mapping($gallery,heading,transform),mapping($gallery,heading,transform),uppercase);
        margin: 0 0 21px 0;
        text-align: if(mapping($gallery,heading,align),mapping($gallery,heading,align),left);
        display: block;
        line-height: 1;
    }

    p {
        @if mapping($gallery,description,isDefaultDesFont) == true {
            font-size: mapping($gallery,description,fontSize);
        }  @else{
            font-family: if(mapping($gallery,description,fontFamily),mapping($gallery,description,fontFamily),"Arial");
            font-size: if(mapping($gallery,description,fontSize),mapping($gallery,description,fontSize),1.35714em);
            font-weight: if(mapping($gallery,description,fontWeight),mapping($gallery,description,fontWeight),300);

        }
        color: if(mapping($gallery,description,color),mapping($gallery,description,color),$page-sub-title);
        text-align: if(mapping($gallery,description,align),mapping($gallery,description,align),left);
        margin: 10px 0 0;
    }
}

.gallery-extnd-tile{
    padding: mapping($layout,main,sectionMargin);
        @media #{$medium-down} {
            padding: mapping($layout,main,tabSectionMargin);
        }
        @media #{$small-down} {
            padding: mapping($layout,main,mobSectionMargin);
        }
        ul {
            &>li {
                // clear: none !important;
                @extend .col-sm-#{mapping($gallery, defaultListCol, mobItem)+1};
                @extend .col-md-#{mapping($gallery, defaultListCol, tabItem)+1};
                @extend .col-lg-#{mapping($gallery, defaultListCol, desItem)+1};
                @media #{$medium-only}{
                    $tabItem : if(mapping($gallery, defaultListCol, tabItemToShow), mapping($gallery, defaultListCol, tabItemToShow), mapping($gallery, defaultListCol, itemToShow));
                    &:nth-of-type(1n+#{$tabItem+1}) {
                        display: none;
                    }
                }
                @media #{$medium-up}{
                    &:nth-of-type(1n+#{mapping($gallery, defaultListCol, itemToShow)+1}) {
                        display: none;
                    }
                }
                @media #{$small-down}{
                    $mobItem : if(mapping($gallery, defaultListCol, mobItemToShow), mapping($gallery, defaultListCol, mobItemToShow), mapping($gallery, defaultListCol, itemToShow));
                    &:nth-of-type(1n+#{$mobItem+1}) {
                        display: none;
                    }
                }
            }
        }
        .view-all{
            @if mapping($gallery, defaultListCol,isViewAllHide) == true{
                display: none !important;
            }
        }
}
//list structure
.gallery-tile-list {
    ul {
        @extend .row;

        .row:before,
        .row:after {
            display: none !important;
        }

        display:block;
        width:100%;
        margin:0;

        &>li {
            @extend .col-sm-3;
            @extend .col-md-3;
            @extend .col-lg-3;
            text-align: center;
            //padding:10px;
            padding: 0;
            margin: 0;
            position: relative;

            &:nth-of-type(1n+5) {
                display: none;
            }

            @media #{$medium-down} {
                width: 50%;
            }

            @media #{$small-down} {
                width: 100%;
                display: inline-block;
            }

            @media #{$medium-up} {
                &.column-3 {
                    width: 33.33333%;
                }

                &.column-4 {
                    width: 25%;
                }
            }

            //character Name
            a {
                display: block;
                position: relative;
                margin: em(15);

                &.btn-primary {
                    color: $white;
                }

                &:hover,
                &:focus,
                &.active {
                    &.gallery-image {
                        img {
                            opacity: 1;
                        }
                    }
                }

                .gallery-image {
                    background-color: $white;
                    @extend .grid-border-style;
                    img {
                        // max-height: 100%;
                        max-width: 100%;
                        width:100%;
                        .image-container {
                            &:hover {
                                opacity: 1;
                            }
                        }
                    }
                }

                span {
                    @if mapping($gallery,listTitle,isBgnd) == false{
                        background: none;
                        @include border-radius(0);
                    } @else{
                        background-color: $character-label-bg;
                    }
                    @include transition(350ms ease);
                    text-transform: uppercase;
                    //font-size: em(28.01);
                    padding: 0 em(2.5);
                    //color: $character-label;
                    display: block;
                    letter-spacing: em(1);
                    font-family: mapping($gallery,listTitle,fontFamily);
                    text-align:if(mapping($gallery,listTitle,align), mapping($gallery,listTitle,align),left);
                    font-size:if(mapping($gallery,listTitle,fontSize), mapping($gallery,listTitle,fontSize),em(28.01));
                    font-weight: mapping($gallery,listTitle,fontWeight);
                    color: if(mapping($gallery,listTitle, color), mapping($gallery,listTitle,color),$character-label);

                    &.btn-primary {
                        @if mapping($gallery, main, sameasDefaultBtn) == true {
                            @extend .theme-btn;
                        } @else{
                            @extend .btn-primary;
                            font-size: em(14);
                            text-align: center;
                            padding: 10px 5px;
                            display: inline-block;
                        }
                    }
                }
            }
        }
    }
}

.gallery-image {

    &:hover,
    &:focus,
    &.active {
        img {
            opacity: 0.5;

            &.image-container {
                opacity: 1;
            }
        }
    }
}

.view-all {
    display: none;
    text-align: right;
    margin: em(20) 0;

    @media #{$small-down} {
        text-align: center;
    }
}

.show-more {
    display: none;
    width: 100%;
    text-align: center;
    padding: em(24);
    position: relative;
    z-index: 1;

    a {
        @if mapping($gallery, primaryBtn, isExtendSecondaryBtn) == true {
            @extend .secondary-btn;
        } @else if mapping($gallery, primaryBtn, isExtendPrimaryBtn) == true {
            @extend .theme-btn;
        } @else{
            @extend .btn-show-more;
        }
    }
}

.margin-LR0 {
    margin-left: 0 !important;
    margin-right: 0 !important;
}
.tiles-center {
    padding: em(6);
}

//common structure
.play-tiles-gallery {
    padding: 0;
    width: 100%;
    margin: 0 auto !important;
    background-color: $white;
    @extend .margin-LR0;

    .gallery-title {
        @extend .gallery-headings;
    }

    .gallery-tile {
        @extend .gallery-tile-list;
    }
    &.default{
         
    }

    @media #{$medium-down} {
        width: 100%;
    }
}

//dont include any class into this
.play-tiles-gallery {
    // overflow: hidden;
    @if mapping($gallery, main,bgnd) == true {
        background:url(mapping($gallery, main,bgnd));
    } @else{
        background-color: transparent;
    }

    &.play-character-landing-block {

        ul {
            li {
                display: block !important;
            }

            .playing-overlay {
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;

                span {
                    position: absolute;
                    top: 7px;
                    left: 7px;
                    font-size: 1em;
                    font-family: "Arial", sans-serif;
                    white-space: nowrap;
                    display: inline-block;
                    padding: em(7);
                    color: $character-label;
                    background-color: $character-label-bg;
                    z-index: 1;
                    letter-spacing: 1px;
                }
            }
        }
    }

    &#play-game {
        background: url($game-gallery-bg) 0 0 repeat;

        .gallery-tile {
            h2 {
                color: $violet;
                //padding:40px 55px 25px 55px;
                margin: 0;
                padding: 20px 54px 0;
                font-size: 2.5em;
            }
        }

        ul {
            padding: 0 40px 35px 40px;

            li {
                padding: em(15);
            }
        }
    }
}

//gallery component classes
//default
.default {
    .gallery-tile {
        ul {
            &>li {
                
            }
        }
    }
}

//retailers
.play-retailers {
    margin: 0px auto;
    @if mapping($gallery,retailers, isImageAfter) == true {
        position: relative;
        &:after{
            content: "";
            position: absolute;
            width: 100%;
            top: mapping($gallery,retailers, afterBgndTop);
            bottom: mapping($gallery,retailers, afterBgndBottom);
            height: mapping($gallery,retailers, afterBgndHght);
            background: mapping($gallery,retailers, afterBgnd);
            background-size: mapping($gallery,retailers, afterBgndSize);
            z-index: mapping($gallery,retailers, afterBgndZindx);
            @media #{$medium-down}{
                background-size: mapping($gallery,retailers, afterBgndSizeMob);
            }
        }
    }
    .gallery-title {
        @if mapping($gallery,retailers, isImageAfter) == true {
            position: relative;
            z-index: 1;
        }
        h2 {
            text-align: center;
        }
    }

    .gallery-tile {
        @if mapping($gallery,retailers, isImageAfter) == true {
            position: relative;
            z-index: 1;
        }
        text-align: mapping($gallery, retailers, tileAlign);
        ul {
            background: mapping($gallery, retailers, tileBgnd);
            display: mapping($gallery, retailers, tileDisplay);
            width: mapping($gallery, retailers, tilewidth);
            margin: mapping($gallery, retailers, tileMargin);
            @if mapping($gallery, retailers, applyBordrToParnt) == true {
                @extend .grid-border-style;                
            }
            @media #{$small-up} {
                padding: 0 em(50);
            }

            &>li {
                @if mapping($gallery, retailers, isColConfigTrue) == true and map-get($gallery, retailersListCol) {
                    @extend .col-lg-#{mapping($gallery, retailersListCol, desItem)};   
                    @extend .col-md-#{mapping($gallery, retailersListCol, tabItem)};
                    @extend .col-sm-#{mapping($gallery, retailersListCol, mobItem)};
                }
                @else{
                    &:nth-of-type(1n+5) {
                        display: block;
                    }
    
    
                    @media #{$small-up} {
                        padding: 0 em(25);
                    }
    
                    @media #{$small-down} {
                        width: 50%;
                        float: left;
                        margin: 0;
                    }
                }
                margin:0px auto;

                a {
                    margin: 0;

                    span {
                        display: inline;

                        &.sr-only {
                            padding: 0;
                            margin: 0;
                        }
                    }

                    .gallery-image {
                        background-color: transparent;
                        @if mapping($gallery, retailers, applyBordrToParnt) == true {
                            border: 0;
                            @include border-radius(0);               
                        }
                    }
                }
            }
        }

        .view-all {
            display: none !important;
        }
    }
}


