                        @media #{$medium-only}{
                            $tabItem : if(mapping($gallery, charactersListCol, tabItemToShow), mapping($gallery, charactersListCol, tabItemToShow), mapping($gallery, charactersListCol, itemToShow));
                            &:nth-of-type(1n+#{$tabItem+1}) {
                                display: none;
                            }
                        }
                        @media #{$medium-up}{
                            &:nth-of-type(1n+#{mapping($gallery, charactersListCol, itemToShow)+1}) {
                                display: none;
                            }
                        }
                        @media #{$small-down}{
                            $mobItem : if(mapping($gallery, charactersListCol, mobItemToShow), mapping($gallery, charactersListCol, mobItemToShow), mapping($gallery, charactersListCol, itemToShow));
                            &:nth-of-type(1n+#{$mobItem+1}) {
                                display: none;
                            }
