//image-gallery with download button
.play-download {
    position: relative;
    .gallery-tile {
        .character-label {
            @extend .gallery-headings;
            padding: 0 em(3) em(3);

            h2 {
                font-size: em(21);
                text-overflow: ellipsis;
                overflow: hidden;
                width: 100%;
                height: 32px;
                white-space: nowrap;
                margin: if(mapping($gallery, download, labelMargin), mapping($gallery, download, labelMargin), em(0));
                padding: em(3) em(0);
            }

            //height variables
            $font-size:1em;
            $line-height:1.4;
            $lines-to-show:3;

            p {
                display: none;

                &:first-child {
                    display: block;
                    /* Fallback for non-webkit */
                    display: -webkit-box;
                    width: 100%;
                    height: $font-size*$line-height*$lines-to-show;
                    /* Fallback for non-webkit */
                    margin: 0 auto;
                    font-size: $font-size;
                    line-height: $line-height;
                    -webkit-line-clamp: $lines-to-show;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }

            ul {
                &>li {
                    margin-bottom: em(10);

                    a {
                        margin: 5px;
                        color: $white;

                        @media #{$medium-down} {
                            margin: 0;
                        }

                        .gallery-image {
                            background-color: transparent;
                        }
                    }

                    .hover-overlay {
                        position: absolute;
                        top: 0em;
                        left: 0;
                        bottom: 0;
                        right: 0;
                        background: rgba(95, 37, 159, .5);
                        z-index: 99;
                        opacity: 0; //@include css3(transform, scale(1.1));
                        // border-radius:50%;
                        width: 100%;
                        margin: auto;
                        @include transition(350ms ease);

                        button {
                            //@include center();
                            -webkit-transform: translate(-50%, -50%);
                            -moz-transform: translate(-50%, -50%);
                            -ms-transform: translate(-50%, -50%);
                            -o-transform: translate(-50%, -50%);
                            transform: translate(-50%, -50%);
                            top: 50%;
                            left: 50%;
                            position: absolute;
                            padding: .5em;
                            font-family: ArialBold, Arial, sans-serif;
                            font-size: em(12);
                            letter-spacing: 1px;
                        }
                    }

                    &:hover {
                        .hover-overlay {
                            opacity: 1;
                        }
                    }
                }
            }
        }
        @if mapping($gallery, download, isImageWidthAuto) == true {
            .gallery-image{
                display: inline-block;
                img{
                    width: auto;
                }
            }
        }
        @if mapping($gallery, download, isColConfigTrue) == true {
            //@extend .gallery-extnd-tile;
            ul {
                &>li {
                    // clear: none !important;
                    @if(map-get($gallery, downloadListCol)){
                        @extend .col-sm-#{mapping($gallery, downloadListCol, mobItem)+1};
                        @extend .col-md-#{mapping($gallery, downloadListCol, tabItem)+1};
                        @extend .col-lg-#{mapping($gallery, downloadListCol, desItem)+1};
                        @media #{$medium-only}{
                            $tabItem : if(mapping($gallery, downloadListCol, tabItemToShow), mapping($gallery, downloadListCol, tabItemToShow), mapping($gallery, downloadListCol, itemToShow));
                            &:nth-of-type(1n+#{$tabItem+1}) {
                                display: none;
                            }
                        }
                        @media #{$medium-up}{
                            &:nth-of-type(1n+#{mapping($gallery, downloadListCol, itemToShow)+1}) {
                                display: none;
                            }
                        }
                        @media #{$small-down}{
                            $mobItem : if(mapping($gallery, downloadListCol, mobItemToShow), mapping($gallery, downloadListCol, mobItemToShow), mapping($gallery, downloadListCol, itemToShow));
                            &:nth-of-type(1n+#{$mobItem+1}) {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }
    .gallery-title{
        h2 {
            @if mapping($gallery, download, isDefaultHeadingFont) == true {
                @extend .page-heading-font;
            }
        }
        p{
            @if mapping($gallery, download, isDefaultHeadingFont) == true {
                @extend .page-heading-font;
            }
        }
    }
}

